==============================================================================*/
.root {
  overflow: hidden; }

/*============================================================================
	#Header
==============================================================================*/
/*============================================================================
	#Footer
==============================================================================*/
.header__overlay._err404 {
  transform: translateY(0) !important; }

.root__err404 {
  margin-top: 110px; }

.err404 {
  position: relative; }
  .err404 .container {
    position: relative; }
  .err404__content {
    padding: 140px 0 130px;
    max-width: 500px;
    margin-left: auto;
    margin-right: 190px; }
  .err404__title {
    font-weight: 500;
    font-size: 4rem; }
  .err404__num {
    font-size: 20rem;
    line-height: 1;
    font-weight: 700;
    display: inline-block;
    margin-bottom: 20px; }
  .err404__text {
    max-width: 350px;
    margin-bottom: 35px; }
  .err404 .button-svg {
    display: inline-block;
    margin-bottom: 15px; }
  .err404 .button-svg__text {
    width: 100%;
    text-align: center; }
  .err404__bg {
    position: absolute;
    top: 140px;
    left: -400px; }
  .err404::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: -50%;
    width: calc(100% + 760px);
    height: 100%;
    background: #f5f5f5; }

@media (max-width: 1366px) {
  .root__err404 {
    margin-top: 96px; } }

@media (max-width: 1140px) {
  .root__err404 {
    margin-top: 86px; } }

@media (max-width: 1199.98px) {
  .err404__content {
    margin-right: 100px;
    margin-left: auto;
    padding: 110px 0 100px; } }

@media (max-width: 991.98px) {
  .err404__content {
    margin: 0 auto; }
  .err404__title {
    font-size: 3.8rem; }
  .err404__bg {
    display: none; } }

@media (max-width: 575.98px) {
  .err404__content {
    padding: 80px 0; }
  .err404__num {
    font-size: 36vw; } }

@media (max-width: 414.98px) {
  .err404__title {
    font-size: 3.4rem; }
  .err404__num {
    font-size: 40vw; }
  .err404 .button-svg {
    width: 100%;
    max-width: 350px; } }

.modal {
  display: none; }

